<template>
  <div class="select-answer">
    <ul>
      <li>
        <div v-for="item in srvItems" :key="item.srvItemsId">
          <input type="radio" :name="name" :value="item.srvItemsId" v-model="respVal.srvItem"/>
          <h4>{{item.srvItemNm}}</h4>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'SelectAnswer',
  props: ['vo', 'index'],
  data () {
    return {
      name: `answer#${this.vo.srvMstrId}#${this.vo.srvMtritemsId}`,
      srvItems: [],
      respVal: {
        srvMstrId: this.vo.srvMstrId,
        srvMtritemsId: this.vo.srvMtritemsId,
        srvItem: null
      }
    }
  },
  created () {
    this.srvItems = this.vo.srvItems
  },
  updated () {
    this.$eventBus.$emit('tr_inputRespVal', this.respVal, this.index)
  }
}
</script>

<style scoped>
 div.select-answer {
   font-size: 15px;
   height: auto;
 }
 div.select-answer ul {
   list-style: none;
   padding: 10px 0px 10px 0px;
 }
 div.select-answer ul li div {
   margin: 10px 0px 10px 0px;
 }
 div.select-answer ul li div input[type="radio"] {
   position: relative;
   margin: 0px 0px 0px 0px;
   vertical-align: middle;
 }
 div.select-answer ul li div input[type="text"] {
   position: relative;
   margin: 0px 0px 0px 0px;
   vertical-align: middle;
   border: 0;
   padding: 10px 10px 10px 0px;
   width: 20em;
   top: -1px;
 }
 div.select-answer ul li div h4 {
   position: relative;
   left: 18px;
   top: -16px;
   margin: 0px;
 }
</style>



